import s from './SensorsStatus.module.css'
import greenEllipse from './../../../icons/content/greenEllipse.svg'
import redEllipse from './../../../icons/content/redEllipse.svg'

let SensorsStatus = (props) => {
    return (
        <div className={s.wrapper}>
            <div className={s.sensorsContent}>
                <h2></h2>
                <div style={{marginTop: '20px'}}> CO2 - MH-Z19B: {props.status.mhz19b < props.time ? '' : ' '} <img src={props.status.mhz19b < props.time ? greenEllipse : redEllipse}/></div>
                <div>  - DS18B20: {props.status.ds18b20 < props.time ? '' : ' '} <img src={props.status.ds18b20 < props.time ? greenEllipse : redEllipse}/></div>
                <div>  - PM Sensor SDS011: {props.status.sds011 < props.time ? '' : ' '} <img src={props.status.sds011 < props.time ? greenEllipse : redEllipse}/></div>
                <div>  - TGS2611: {props.status.tgs2611 < props.time ? '' : ' '} <img src={props.status.tgs2611 < props.time ? greenEllipse : redEllipse}/></div>
                <div>     - SHT30: {props.status.sht30 < props.time ? '' : ' '} <img src={props.status.sht30 < props.time ? greenEllipse : redEllipse}/></div>
                <div>     - BMP280: {props.status.bmp280 < props.time ? '' : ' '} <img src={props.status.bmp280 < props.time ? greenEllipse : redEllipse}/></div>
            </div>
        </div>
    );
}



export default SensorsStatus;
